/**
 * WordPress dependencies
 */
import { useDispatch, useSelect } from "@wordpress/data";
import { useEffect, useRef } from "@wordpress/element";
/**
 * External dependencies
 */
import { useLocation } from "react-router-dom";
/**
 * Internal dependencies
 */
import { stores } from "@/globalConstant";
import { useIsMobile } from "@/common/hooks/useMobile";
import useSidebarData from "./useSidebarData";
export const useSidebarController = () => {
    // Global store state
    const { isExpanded, collapsedGroups } = useSelect((select) => select(stores === null || stores === void 0 ? void 0 : stores.sidebar).getSidebarState(), []);
    const location = useLocation();
    const { autoCollapseRoutes } = useSidebarData();
    const previousPathRef = useRef(null);
    const { setSidebarState } = useDispatch(stores === null || stores === void 0 ? void 0 : stores.sidebar);
    const setIsExpanded = (expanded) => setSidebarState({ isExpanded: expanded });
    const toggleGroup = (groupId) => setSidebarState({
        collapsedGroups: Object.assign(Object.assign({}, collapsedGroups), { [groupId]: !(collapsedGroups === null || collapsedGroups === void 0 ? void 0 : collapsedGroups[groupId]) }),
    });
    const isMobile = useIsMobile();
    // Auto-collapse on first mount for mobile
    const hasAutoCollapsed = useRef(false);
    useEffect(() => {
        if (isMobile && isExpanded && !hasAutoCollapsed.current) {
            setSidebarState({ isExpanded: false });
            hasAutoCollapsed.current = true;
        }
    }, [isMobile, isExpanded, setSidebarState]);
    // Toggle custom sidebar when WP admin bar hamburger is clicked
    useEffect(() => {
        const adminBar = document.getElementById("wpadminbar");
        if (!adminBar)
            return;
        const clickHandler = (e) => {
            const target = e.target;
            if (!target)
                return;
            const match = target.closest([
                "#wp-admin-bar-menu-toggle",
                "#wp-admin-bar-menu",
                "#wpadminbar .ab-top-menu > li#wp-admin-bar-menu-toggle > a.ab-item",
                '#wpadminbar .ab-top-menu > li > a.ab-item[aria-label="Main menu"]',
                '#wpadminbar .ab-top-menu > li > a.ab-item[aria-label="Menu"]',
            ].join(", "));
            if (match) {
                setIsExpanded(!isExpanded);
            }
        };
        adminBar.addEventListener("click", clickHandler);
        return () => {
            adminBar.removeEventListener("click", clickHandler);
        };
    }, [isExpanded]);
    // Auto-collapse sidebar for specific routes (only on route change)
    useEffect(() => {
        const currentPath = location.pathname;
        const previousPath = previousPathRef.current;
        // Auto-collapse when:
        // 1. Path changes (navigation), OR
        // 2. Initial mount (previousPath is null)
        if (currentPath !== previousPath) {
            const shouldAutoCollapse = autoCollapseRoutes.some((route) => currentPath.startsWith(route));
            if (shouldAutoCollapse && isExpanded) {
                setIsExpanded(false);
            }
            // Update previous path
            previousPathRef.current = currentPath;
        }
    }, [location.pathname, autoCollapseRoutes, isExpanded, setIsExpanded]);
    return {
        isExpanded,
        setIsExpanded,
        collapsedGroups,
        toggleGroup,
        isMobile,
    };
};
